.text-brand {
  color: var(--brand);
}

.text-accent {
  color: var(--accent);
}

.text1 {
  color: var(--text1);
}

.text2 {
  color: var(--text2);
}

.text3 {
  color: var(--text3);
}

.surface1 {
  background-color: var(--surface1);
}

.surface2 {
  background-color: var(--surface2);
}

.surface3 {
  background-color: var(--surface3);
}

.surface4 {
  background-color: var(--surface4);
}

html,
body {
  color: var(--text1);
  background-color: var(--surface5);
}

body {
  font-family: var(--ff-s);
  font-size: var(--fs-300);
  line-height: 1.6;
}
@media (min-width: 50em) {
  body {
    font-size: var(--fs-400);
  }
}
@media (min-width: 80em) {
  body {
    font-size: var(--fs-500);
  }
}

h1,
h2,
p {
  margin-top: 0;
  margin-bottom: 0.5em;
}

h1 {
  font-family: var(--ff-s);
  font-weight: var(--fw-b);
  font-size: 2.5rem;
  color: var(--clr-accent);
  letter-spacing: 0;
  line-height: var(--lh-1);
  margin-right: calc(var(--ls) * -1);
}
@media (min-width: 50em) {
  h1 {
    font-family: var(--ff-s);
    font-weight: var(--fw-b);
    font-size: 4.25rem;
    color: var(--clr-accent);
    letter-spacing: 0;
    line-height: var(--lh-1);
    margin-right: calc(var(--ls) * -1);
  }
}

h2 {
  font-family: var(--ff-ss);
  font-weight: var(--fw-n);
  font-size: 2rem;
  color: var(--accent);
  letter-spacing: var(--ls);
  line-height: var(--lh-1);
  text-transform: uppercase;
  margin-right: calc(var(--ls) * -1);
}
@media (min-width: 50em) {
  h2 {
    font-family: var(--ff-ss);
    font-weight: var(--fw-n);
    font-size: 3rem;
    color: var(--accent);
    letter-spacing: var(--ls);
    line-height: var(--lh-1);
    text-transform: uppercase;
    margin-right: calc(var(--ls) * -1);
  }
}

/* ALL FOR  */
body {
  max-width: 100%;
  margin: 0 auto;
  overflow-x: hidden;
}

nav {
  position: fixed;
  top: 0;
  left: -80%;
  bottom: 0;
  right: 0;
  width: 100%;
  z-index: 10;
  display: grid;
  column-gap: 2%;
  grid-template-columns: 78% 20%;
  grid-template-areas: "menu icon";
}
nav .icon {
  grid-area: icon;
}
nav .menu {
  grid-area: menu;
}
nav .icon {
  background-color: transparent;
  border: none;
  margin-left: 2em;
  height: 3em;
}
nav .hamburger {
  display: block;
}
nav .close-icon {
  display: none;
}
nav .menu {
  margin: 0;
  padding: 0;
  background-color: var(--surface4);
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  text-align: center;
  width: auto;
  font-family: var(--ff-ss);
  font-weight: var(--fw-n);
  font-size: var(--fs-800);
  color: var(--accent);
  letter-spacing: var(--ls);
  line-height: 1.4;
  text-transform: uppercase;
  margin-right: calc(var(--ls) * -1);
}
nav .menu li {
  width: 100%;
  justify-self: stretch;
  padding-block: 3rem;
  border-bottom: 1px solid var(--accent);
}
nav .menu li a {
  margin: 0;
  display: inline-block;
  text-decoration: none;
  justify-self: stretch;
  width: 100%;
  color: var(--accent);
}
nav .menu li:hover {
  background-color: var(--surface1);
}

.nav-is-open nav {
  left: 0;
}
.nav-is-open .hamburger {
  display: none;
}
.nav-is-open .close-icon {
  display: block;
}

/* ALL FOR THE WELCOME */
.welcome {
  background-image: url(../../img/header-bg.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  display: flex;
  flex-direction: column;
  place-items: center;
  place-content: center;
  text-align: center;
  height: 100vh;
}
.welcome .welcome__text {
  font-family: var(--ff-s);
  font-weight: var(--fw-n);
  font-size: 1.25rem;
  color: var(--text1);
  letter-spacing: var(--ls);
  line-height: var(--lh-1);
  margin-right: calc(var(--ls) * -1);
}
.welcome .welcome__title {
  font-family: var(--ff-ss);
  font-weight: var(--fw-n);
  font-size: 5rem;
  color: var(--accent);
  letter-spacing: var(--ls);
  line-height: 1.1;
  text-transform: uppercase;
  margin-right: calc(var(--ls) * -1);
}
@media (min-width: 50em) {
  .welcome .welcome__text {
    font-family: var(--ff-s);
    font-weight: var(--fw-n);
    font-size: 2rem;
    color: var(--text1);
    letter-spacing: var(--ls);
    line-height: var(--lh-1);
    margin-right: calc(var(--ls) * -1);
  }
  .welcome .welcome__title {
    font-family: var(--ff-ss);
    font-weight: var(--fw-n);
    font-size: 9rem;
    color: var(--accent);
    letter-spacing: var(--ls);
    line-height: 1.1;
    text-transform: uppercase;
    margin-right: calc(var(--ls) * -1);
  }
}
@media (min-width: 80em) {
  .welcome .welcome__text {
    font-family: var(--ff-s);
    font-weight: var(--fw-n);
    font-size: 3rem;
    color: var(--text1);
    letter-spacing: var(--ls);
    line-height: var(--lh-1);
    margin-right: calc(var(--ls) * -1);
  }
  .welcome .welcome__title {
    font-family: var(--ff-ss);
    font-weight: var(--fw-n);
    font-size: 12rem;
    color: var(--accent);
    letter-spacing: var(--ls);
    line-height: 1.1;
    text-transform: uppercase;
    margin-right: calc(var(--ls) * -1);
  }
}
.welcome .welcome__text::before,
.welcome .welcome__title::after {
  content: "";
  display: block;
  height: 3px;
  width: 50%;
  background-color: hsla(var(--clr-brand), 50%, 50%, 0.07);
}
.welcome .welcome__text::before {
  margin: 0 auto 5rem;
}
.welcome .welcome__title::after {
  margin: 5rem auto 0;
}

/* ALL FOR THE INTRO */
.intro {
  display: grid;
  grid-template-columns: minmax(4em, 1Fr) minmax(auto, 2fr) minmax(4em, 1fr);
  overflow: hidden;
  width: 100%;
}
.intro img {
  max-width: 100%;
  height: 100%;
  display: block;
}
.intro .intro__left {
  grid-area: left;
  grid-column: 1/2;
}
.intro .intro__middle {
  grid-area: middle;
  grid-column: 2/3;
}
.intro .intro__right {
  grid-area: right;
  grid-column: 3/4;
}
.intro .intro__middle {
  display: flex;
  padding-block: 2vw;
  padding-inline: 0.5em;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  background-color: var(--surface1);
}
.intro .intro__middle h2 {
  font-family: var(--ff-ss);
  font-weight: var(--fw-n);
  font-size: 2.125rem;
  color: var(--text3);
  letter-spacing: var(--ls);
  line-height: var(--lh-1);
  text-transform: uppercase;
  margin-right: calc(var(--ls) * -1);
}
.intro .intro__middle h2::after {
  content: "";
  display: block;
  margin: 0.75em auto 0 auto;
  height: 1px;
  width: 20%;
  background-color: hwb(0 100% 7%/0.2);
}
.intro .intro__middle p {
  color: var(--text3);
}

/* ALL FOR THE ABOUT  */
.about-me {
  padding-inline: 2em;
  padding-block: 6em;
  display: grid;
  grid-template-columns: minmax(5rem, 1fr) 1fr 2fr;
  row-gap: 1em;
}
@media (min-width: 50em) {
  .about-me {
    grid-template-columns: 6rem 6rem 6rem 2fr;
    grid-template-rows: 8rem 6rem 2fr;
    column-gap: 1.5rem;
  }
}
.about-me .about__image {
  grid-row: 1/2;
  grid-column: 1/3;
}
@media (min-width: 50em) {
  .about-me .about__image {
    grid-column: 1/2;
    grid-row: 1/4;
  }
}
.about-me .about__title {
  grid-row: 1/2;
  grid-column: 2/3;
  align-self: end;
}
@media (min-width: 50em) {
  .about-me .about__title {
    grid-row: 1/4;
    align-self: start;
  }
}
.about-me .about__subtitle {
  grid-row: 2/3;
  grid-column: 1/4;
}
@media (min-width: 50em) {
  .about-me .about__subtitle {
    grid-column: 4/6;
    grid-row: 2/4;
  }
}
.about-me .about__text {
  grid-row: 3/4;
  grid-column: 1/4;
}
@media (min-width: 50em) {
  .about-me .about__text {
    grid-column: 4/6;
    grid-row: 3/4;
  }
}
.about-me .about__title {
  color: var(--accent);
}
.about-me .about__subtitle {
  color: var(--text1);
}
.about-me .about__text {
  font-family: var(--ff-s);
  font-weight: var(--fw-n);
  font-size: 1rem;
  color: var(--text2);
  letter-spacing: var(--ls);
  line-height: var(--lh-1);
  margin-right: calc(var(--ls) * -1);
}

/* ALL FOR PORTFOLIO */
.portfolio {
  background-color: var(surface2);
  text-align: center;
  margin: 3em auto;
  width: auto;
}
.portfolio h1 {
  color: var(--accent);
}
.portfolio .portfolio__items {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 50em) {
  .portfolio .portfolio__items {
    grid-template-columns: repeat(4, 1fr);
  }
}
.portfolio .portfolio__items .featured {
  grid-column: 1/span 2;
  grid-row: 1/span 2;
}
.portfolio .portfolio__items .portfolio__item {
  position: relative;
  overflow: hidden;
}
.portfolio .portfolio__items .portfolio__item img {
  display: block;
  width: 100%;
}
.portfolio .portfolio__items .portfolio__item figcaption {
  position: absolute;
  top: 100%;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 2em;
  display: flex;
  flex-direction: column;
  text-align: left;
  justify-content: flex-end;
  color: var(--text3);
  background-color: hsla(var(--hue-brand), 50%, 50%, 0.6);
  overflow: hidden;
}
.portfolio .portfolio__items .portfolio__item figcaption .portfolio__title {
  color: var(--text3);
}
.portfolio .portfolio__items .portfolio__item figcaption .portfolio__desc {
  display: none;
}
@media (min-width: 50em) {
  .portfolio .portfolio__items .portfolio__item figcaption .portfolio__desc {
    display: block;
  }
}
.portfolio .portfolio__items .portfolio__item figcaption .portfolio__link {
  color: var(--text3);
  font-weight: var(--fw-b);
  font-size: var(--fs-400);
  text-decoration: none;
  border-bottom: 3xp solid var(--text3);
}
.portfolio .portfolio__items .portfolio__item figcaption .portfolio__link::after {
  content: url(../../img/arrow-white.svg);
  display: inline-block;
  height: 0.8em;
  width: 0.8em;
  margin-left: 0.25em;
}
.portfolio .portfolio__items .portfolio__item:hover figcaption, .portfolio .portfolio__items .portfolio__item:focus figcaption {
  top: 0;
}

/* ALL FOR CONTACT */
.contact {
  width: 90%;
  margin: 0 auto;
  max-width: 50em;
  padding: 5em 0;
  text-align: center;
}
.contact h1 {
  color: var(--accent);
}
.contact form {
  display: flex;
  flex-direction: column;
}
@media (min-width: 50em) {
  .contact form {
    display: grid;
    column-gap: 1em;
    grid-template-areas: "nom eml" "msg msg" "btn btn";
  }
  .contact form .name {
    grid-area: nom;
  }
  .contact form .email {
    grid-area: eml;
  }
  .contact form .message {
    grid-area: msg;
  }
  .contact form .message textarea {
    height: 10em;
  }
  .contact form .button-form {
    grid-area: btn;
  }
}

label {
  display: block;
  margin-top: 1em;
  color: var(--text1);
  text-align: left;
}

input,
textarea {
  box-sizing: border-box;
  display: block;
  padding: 0.75em;
  font-size: 1.2rem;
  width: 100%;
  background-color: hsl(var(--hue-brand), 7%, 97%);
  border: 1px solid var(--text1);
}

.btn {
  display: inline-block;
  width: auto;
  margin-top: 1em;
  margin-left: auto;
  background-color: var(--accent);
  border: none;
  font-family: var(--ff-ss);
  font-weight: var(--fw-n);
  font-size: 2rem;
  color: var(--text3);
  letter-spacing: var(--ls);
  line-height: var(--lh-1);
  text-transform: uppercase;
  margin-right: calc(var(--ls) * -1);
  padding: 0.5em 1.5em;
}

/* ALL FOR THE FOOTER */
footer {
  background-color: var(--surface1);
  padding: 5em 0;
  text-align: center;
  color: var(--text3);
}

.social-icons {
  width: 90%;
  margin: 0 auto;
  max-width: 15em;
  display: flex;
  justify-content: space-between;
  margin: 1.75em auto 4em;
}

.social-icon {
  width: 25%;
  height: 25%;
}

.copyright::before {
  content: "";
  display: block;
  height: 1px;
  background-color: hwb(0 100% 7%/0.2);
  margin: 4em auto;
  width: 25%;
}

/* ALL FOR THE MODAL */
.modal {
  position: fixed;
  top: 2em;
  bottom: 2em;
  left: 2em;
  right: 2em;
  z-index: 25;
  background-color: var(--surface2);
  box-shadow: 0 0 1em 2em rgba(0, 0, 0, 0.5);
  overflow-y: scroll;
  overflow-x: hidden;
  display: none;
}
.modal .modal__btn-close {
  position: fixed;
  background-color: var(--surface5);
  left: 1em;
  top: 1em;
  border: 0;
  border-radius: 50%;
  height: 2em;
  width: 2em;
  padding: 3px 0 0;
  cursor: pointer;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
}
.modal .modal__header {
  padding: 5em 0;
  margin-bottom: 2em;
}
.modal .modal__header:nth-of-type(1) {
  background-image: url(../../img/portfolio-01.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.modal .modal__titlebox {
  padding: 3em 0 3em 3em;
  color: var(--text3);
  background-color: hsla(var(--hue-brand), 50%, 50%, 0.6);
  width: 80%;
}
.modal .modal__titlebox .modal__title {
  display: inline-block;
}
.modal .modal__titlebox .modal__title::after {
  content: "";
  display: block;
  height: 1px;
  background-color: var(--text3);
  width: 100%;
  margin: 0.5em auto;
}
.modal .modal__content {
  width: 90%;
  margin: 0 auto;
  padding: 2em;
  padding-bottom: 2em;
}
.modal .modal__img_full {
  width: 100vw;
  max-width: initial;
  margin: 0 calc(50% - 50vw);
  height: 20vh;
  object-fit: cover;
}
.modal .modal__img_left {
  display: block;
  max-width: 100%;
}
@media (min-width: 50em) {
  .modal .modal__img_left {
    width: 50%;
    float: left;
    margin-right: 2em;
    padding-block: 1em;
  }
}
.modal .modal__img_right {
  display: block;
  max-width: 100%;
}
@media (min-width: 50em) {
  .modal .modal__img_right {
    width: 50%;
    float: right;
    margin-left: 2em;
    padding-block: 1em;
  }
}

#theme {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 1rem;
}
#theme .surface {
  display: grid;
  grid-template-areas: " one   two" " three four";
  gap: 1rem;
  width: 400px;
  height: 400px;
  font-size: 2rem;
}
#theme .surface1 {
  grid-area: one;
}
#theme .surface2 {
  grid-area: two;
}
#theme .surface3 {
  grid-area: three;
}
#theme .surface4 {
  grid-area: four;
}
#theme .surface1,
#theme .surface2,
#theme .surface3,
#theme .surface4 {
  text-align: center;
  padding: 2em;
}
#theme .swatches {
  background-color: var(surface2);
}
#theme .swatch {
  display: inline-block;
  flex-shrink: 0;
  inline-size: 1.5ch;
  block-size: 1.5ch;
  border-radius: 50%;
}
#theme .swatch.text1 {
  background-color: var(--text1);
}
#theme .swatch.text2 {
  background-color: var(--text2);
}
#theme .swatch.text3 {
  background-color: var(--text3);
}
#theme .swatch.text-brand {
  background-color: var(--brand);
}
#theme .swatch.text-accent {
  background-color: var(--accent);
}
#theme .text {
  max-width: 35ch;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
}